        const getDt = (scoreStop) => {

                let delay = 0.25;

                for (let score = 0; score < scoreStop; score++) {
                        if (score + 1 < 30) delay = 0.25;
                        if (score + 1 === 30) delay -= 0.02;
                        if (score + 1 === 40) delay -= 0.02;
                        if (score + 1 === 60) delay -= 0.02;
                        if (score + 1 === 80) delay -= 0.02; //

                        if (score + 1 === 200) {delay = 0.20;

                        }
                        if (score + 1 === 210) delay -= 0.01;
                        if (score + 1 === 220) delay -= 0.01;
                        if (score + 1 === 230) delay -= 0.01;
                        if (score + 1 === 240) delay -= 0.01;
                        if (score + 1 === 250) delay -= 0.01;
                        if (score + 1 === 260) delay -= 0.01;
                        if (score + 1 === 270) delay -= 0.01;
                        if (score + 1 === 280) delay -= 0.01;

                        if (score + 1 === 500) delay = 0.2;

                        if (score + 1 === 510) delay -= 0.02;
                        if (score + 1 === 520) delay -= 0.02;
                        if (score + 1 === 540) delay -= 0.02;
                }
                return delay;
        }
