
import React ,{ memo} from "react";
// import '../../images/OT/styles.css'

import img_Bridge1 from "../../images/Bridge/Bridge1.png";
import img_Bridge2 from "../../images/Bridge/Bridge2.png";
import img_Bridge3 from "../../images/Bridge/Bridge3.png";

import img_live1 from "../../images/Bridge/life1.png";
import img_live2 from "../../images/Bridge/life2.png";
import img_live3 from "../../images/Bridge/life3.png";


import img_gamea from "../../images/OT/Gamea.png";
import img_gameb from "../../images/OT/Gameb.png";

function Driver(bridge,  start,  live) {



    if (start){
        const obj = {}


        obj.Bridge1 = true
        obj.Bridge2 = true
        obj.Bridge3 = true

        obj.live1 = true
        obj.live2 = true
        obj.live3 = true

        obj.gamea = true
        obj.gameb = true


        return obj
    }

    const obj = {}
    obj.Bridge1 = false
    obj.Bridge2 = false
    obj.Bridge3 = false

    obj.live1 = false
    obj.live2 = false
    obj.live3 = false

    // obj.gamea = false
    // obj.gameb = false

    switch (live){

        case 1:
            obj.live1 = true
            break;
        case 2:
            obj.live2 = true
            obj.live1 = true
            break;

        case 3:
            obj.live1 = true
            obj.live2 = true
            obj.live3 = true
            break;
        default:
            break;
    }

    switch (bridge){

        case 0:
            obj.Bridge1 = true
            break;
        case 1:
            obj.Bridge2 = true
            break;

        case 2:
            obj.Bridge3 = true

            break;
        default:
            break;
    }

    return obj;


}

function Player(props){

    // console.log("render Player")
    const obj = Driver(props.posgame, props.start, props.live)




    return (
        <>
            {obj.Bridge1 && <div className="Bridge_Bridge1"><img src={img_Bridge2} alt={"img_Bridge1"}/></div>}
            {obj.Bridge2 && <div className="Bridge_Bridge2"><img src={img_Bridge2} alt={"img_Bridge2"}/></div>}
            {obj.Bridge3 && <div className="Bridge_Bridge3"><img src={img_Bridge2} alt={"img_Bridge3"}/></div>}


            {obj.live1 && <div className="Bridge_life1"><img src={img_live1} alt={"img_live1"}/></div>}
            {obj.live2 && <div className="Bridge_life2"><img src={img_live2} alt={"img_live2"}/></div>}
            {obj.live3 && <div className="Bridge_life3"><img src={img_live3} alt={"img_live3"}/></div>}


            {props.gameAB === 'A' && <div className="Bridge_gamea"><img src={img_gamea} alt={"gamea"} /></div>}
            {props.gameAB === 'B' && <div className="Bridge_gameb"><img src={img_gameb} alt={"gameb"} /></div>}
        </>
    )
}
export default memo(Player);